$hoverBreakMobile: 600px;

.button {
  position: fixed;
  display: block;
  font-size: 20px;
  width: 40px;
  height: 40px;
  border: solid 1px #9da19d;
  right: 2%;
  bottom: 2%;
  border-radius: 6px;
  transition: 1.5s;
  background-color: transparent;
  cursor: pointer;

  @media (max-width: $hoverBreakMobile) {
    width: 25px;
    height: 25px;
  }

  &--hide {
    opacity: 0;
  }

  i {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #9da19d;
    font-size: 25px;

    @media (max-width: $hoverBreakMobile) {
      font-size: 18px;
    }
  }
}
